<template>
  <div>
    <van-list :finished="finished" finished-text="没有更多了">
      <!-- 搜索提示列表 -->
      <van-cell
        @click="cellClick(item)"
        v-for="(item, index) in searchTipsListData"
        :key="index"
        :title="item"
      />
    </van-list>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //  list: [1,2,3,4,5,6],
      finished: true,
    };
  },
  props: ["searchTipsListData"],
  methods: {
    cellClick(val) {
      //  console.log(val);
      this.$emit("cellClick", val);
    },
  },
};
</script>

