<template>
  <div class="cart">
    <Tips />
    <van-checkbox-group v-model="result" :disabled="isEditing">
      <van-checkbox
        :name="item.id"
        v-for="item in cartList"
        :key="item.id"
        @click="changeChecked(item)"
      >
        <van-swipe-cell>
          <van-card
            :num="item.number"
            :price="item.retail_price"
            :title="item.goods_name"
            :thumb="item.list_pic_url"
          />
          <van-stepper v-model="item.number" v-show="isEditing" @change="stepperChange(item)"/>
          <template #right>
            <van-button square type="danger" text="删除" @click="delGood(item.product_id)" />
          </template>
        </van-swipe-cell>
      </van-checkbox>
    </van-checkbox-group>

    <van-submit-bar
      :price="cartTotal.checkedGoodsAmount * 100"
      button-text="提交订单"
      @submit="onSubmit"
    >
      <van-checkbox v-model="checkedAll" :disabled="isEditing"
        >全选</van-checkbox
      >
      <template #tip>
        累计共
        <span>{{ cartTotal.checkedGoodsCount }}</span> 件商品，可点击
        <van-button
          :type="isEditing ? 'danger' : 'primary'"
          size="small"
          @click="editBtn"
        >
          {{ isEditing ? "完成编辑" : "编辑" }} </van-button
        >按钮进行商品数量修改
      </template>
    </van-submit-bar>
  </div>
</template>

<script>
import Tips from "@/components/Tips";
import {
  GetCartListData,
  ChangeGoodChecked,
  StepperChangeData,
  DeleteGood
} from "@/request/api";
export default {
  data() {
    return {
      //全选的控制
      // checkedAll:false,

      //购物车列表数据
      cartList: [],
      //商品总计信息
      cartTotal: [],
      //哪一个商品的checked值为1，就可以把它的id添加到这个数组[控制商品被选中]
      result: [],
      //编辑状态的设置
      isEditing: false,
    };
  },
  components: {
    Tips,
  },
  computed: {
    checkedAll: {
      //什么时候表示全选
      get() {
        return this.result.length == this.cartList.length;
      },
      set(val) {
        //即将要修改成的新的值
        // console.log(val);
        let ids_arr = [];
        this.cartList.map((item) => {
          ids_arr.push(item.product_id);
        });
        ChangeGoodChecked({
          isChecked: val ? 1 : 0, //点击全选框相当于取反操作
          productIds: ids_arr.join(),
        }).then((res) => {
          if (res.data.errno == 0) {
            // console.log(res);
            this.totalFn(res.data.data);
          }
        });
      },
    },
  },
  created() {
    GetCartListData().then((res) => {
      if (res.data.errno == 0) {
        // console.log(res);
        // this.cartList=res.data.data.cartList
        // this.cartTotal=res.data.data.cartTotal
        // // console.log( this.cartList);
        // this.cartList.map(item=>{
        // item.checked==1?this.result.push(item.id):"";
        // })
        this.totalFn(res.data.data);
      }
    });
  },
  methods: {
    editBtn() {
      this.isEditing = !this.isEditing;
    },
    //封装一个方法
    totalFn(data) {
      this.cartList = data.cartList;
      this.cartTotal = data.cartTotal;
      //清空this.result数组，后面的操作不会使这个数组越来越长
      this.result = [];
      this.cartList.map((item) => {
        item.checked == 1 ? this.result.push(item.id) : "";
      });
    },
    onClickEditAddress() {},
    onSubmit() {},
    changeChecked(item) {
      if (this.isEditing) return;
      //总结：为什么要发这个请求？这个请求作用？
      //这个请求告诉了后端我们改变了哪些值，后端收到之后，返回给我们一个处理过后的完整的页面数据（包括总价的结算）
      //我们只需要拿到这个数据，网页上面填写就可以（修改cartList和cartTotal）
      ChangeGoodChecked({
        isChecked: item.checked == 0 ? 1 : 0, //点击复选框相当于取反操作
        productIds: item.product_id,
      }).then((res) => {
        if (res.data.errno == 0) {
          // console.log(res);
          this.totalFn(res.data.data);
        }
      });
    },
    stepperChange(item) {
      // console.log(item);
      StepperChangeData({
        goodsId: item.goods_id,
        id: item.id,
        number: item.number,
        productId: item.product_id,
      }).then((res) => {
        if (res.data.errno == 0) {
          this.totalFn(res.data.data);
        }
      });
    },
    delGood(pid){
       DeleteGood({
         productIds:pid.toString()
       }).then(res=>{
         if(res.data.errno==0){
            this.totalFn(res.data.data);
         }
       })
    }
  },
};
</script>

<style lang="less" scoped>
// .van-checkbox-group[data-v-c028c34c]{
//   background-color: #fff;
//   height: 100%;
// }
/deep/.van-checkbox__label {
  flex: 1;
}
.van-checkbox-group {
  background-color: #fff;
  // padding-bottom: 1.5rem;
  margin-bottom: 1.5rem;
  .van-checkbox {
    padding: 0.1rem 0.15rem;
    border-bottom: 1px solid #ccc;
  }
}
.van-submit-bar {
  bottom: 0.5rem;
  border-bottom: 1px solid #ccc;
}
.van-stepper {
  text-align: right;
}
.van-button--square{
  height: 100%;
}
</style>
