<template>
  <div class="popup">
    <van-search
      v-model="searchVal"
      show-action
      :placeholder="placeholderVal"
      @search="onSearch"
      @cancel="onCancel"
      @input="onInput"
    />
     <!-- @input="onInpt"表示输入框内容发生变化时触发oninput这个事件函数 -->
    <!-- <comp1 v-if="blockShow==1"></comp1>
    <comp2 v-else-if="blockShow==2"></comp2>
    <comp3 v-else></comp3> -->
    <HistoryHot @tagClick="tagClick" v-if="blockShow==1" :historyListData="historyListData" :hotKeywordList="hotKeywordList"/>
     <SearchTipsList @cellClick="cellClick" v-else-if="blockShow==2" :searchTipsListData="searchTipsListData"/>
     <SearchProducts v-else :filterCategory="filterCategory" :goodsList="goodsList" 
     @categoryChange="categoryChange" @priceChange="priceChange" />
  </div>
  
</template>
<script>
import HistoryHot from "../components/HistoryHot";
import SearchTipsList from "../components/SearchTipsList";
import SearchProducts from "../components/SearchProducts.vue"
import { GetSearchPopupData,GetSearchTipsListData,GetGoodsListData } from "@/request/api";
export default {
  data() {
    return {
      searchVal: "",
      placeholderVal:"",
    //   1表示展示历史记录和热门搜索
    //   2表示搜索提示的列表
    //   3表示展示搜索产品的内容
      blockShow:1,
    //   历史记录的列表数据
      historyListData:[],
    //   热门搜索列表数据
      hotKeywordList:[],
      // 搜索实时提示的列表数据
      searchTipsListData:[],
      //搜索产品内容的分类数据
      filterCategory:[],
      //搜索出来的产品内容
      goodsList:[],
      //价格排序：（由高到低或者由低到高）
       order:"desc",
       //分类id
       categoryId:0,
       //默认的排序方式是id还是price
       sort:"id"
    };
  },
  watch:{
   '$route' (to,from){
     console.log(to.path);
   }
  },
  methods: {
    cellClick(val){//和tagClick事件同理，cellClick事件原本可以省略
      // console.log(val);
       this.searchVal=val;//相当于在搜索框输入了val
        this.onSearch(val);
    },
    tagClick(val){
      // console.log(val);
       this.searchVal=val;//相当于在搜索框输入了val
        this.onSearch(val);
       
       
    },
    categoryChange(value){
     this.categoryId=value;
      //console.log(this.categoryId);
     //发送搜索商品的请求
     this.onSearch(this.searchVal);

    },
     priceChange(value){
       this.order=value;//value是desc或asc
       this.sort="price";//以前是按id排序，现在是按价格
        this.onSearch(this.searchVal);
     },
    onSearch(val) {
    this.blockShow=3;
    // console.log(val);
    GetGoodsListData({
      keyword:val,
      page:1,
      size:20,
      order:this.order,
      categoryId:this.categoryId,
      sort:this.sort
      }).then(res=>{
      // console.log(this.searchVal);
      if(res.data.errno==0){
        // console.log(res.data.data);
        this.filterCategory=res.data.data.filterCategory;
        this.goodsList=res.data.data.goodsList;
       //对filterCategory的字段进行处理,id改成value，name改为text
       let newArr=JSON.parse(JSON.stringify(this.filterCategory).replace(/name/g,'text').replace(/id/g,'value'))
      //  console.log(newArr);
       this.filterCategory=newArr;

      }
    })

    //  console.log("onsearch时候执行",val);
    },
    onCancel() {
    //  console.log("oncancel");
     this.$router.replace('/home');
    
    //  this.$store.commit("changeIsShowPopupShadow",false);
    this.shadow();
     
    },
    shadow(){
      this.$store.commit("changeIsShowPopupShadow",false);
    },
    onInput(val){
    //   发送请求，获取实时搜索的文本提示的数据列表
    // console.log(val);
    // console.log(1111)
    this.blockShow=2;
      GetSearchTipsListData({keyword:val}).then(res=>{
          // console.log(res);
          if(res.data.errno == 0){
              // console.log(res.data.data);
              this.searchTipsListData=res.data.data;
           
          }
      })
    // GetSearchTipsListData().then(res=>{
    //     console.log(res)
    //     if(res.errno==0){
    //         // console.log(res);
    //         console.log(1111);
    //     }
    // })
    //  GetTimeData({
    //       keyword: this.iptVal
    //     }).then(res => {
    //       if (res.errno == 0) {
    //         this.listArr = res.data;
    //          console.log(res);
    //       }
    //     })

    }
  },
  components:{
     HistoryHot,
     SearchTipsList,
     SearchProducts,//筛选功能栏
  },
  created(){
   GetSearchPopupData().then(res=>{
    //    console.log(1111);
       if(res.data.errno==0){
          //  console.log(res.data);
           this.placeholderVal=res.data.data.defaultKeyword.keyword;
           this.historyListData=res.data.data.historyKeywordList;
           this.hotKeywordList=res.data.data.hotKeywordList;
       }
   })
  }
};
</script>
<style lang="less" scoped>
.popup {
 width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  background-color: #efefef;
  z-index: 9999;

}
</style>
