<template>
  <div>
<div class="his-hot" v-show="isShowHistory">
    <div class="hd">
      <h4>历史记录</h4>
      <van-icon @click="clearHistory" name="delete" />
    </div>
    <div class="bd">
      <van-tag @click="tagClick(item)" plain type="default" v-for="(item,index) in historyListData" :key="index">{{item}}</van-tag>
      
    </div>
  </div>
  <div class="his-hot">
    <div class="hd">
      <h4>热门搜索</h4>
    </div>
    <div class="bd">
      <!-- 两个事件原理一样 -->
      <van-tag @click="tagClick(item.keyword)" plain type="default" v-for="(item,index) in hotKeywordList" :key="index"
      :class="item.is_hot==1?'red':''">{{item.keyword}}</van-tag>
    </div>
  </div>
  </div>
  





  
</template>

<script>
import { ClearHistory } from "@/request/api"
export default {
  name: "HistoryHot",
   data(){
       return{
         isShowHistory:true
       }
   },
   props:["historyListData","hotKeywordList"],
   methods:{
     tagClick(val){
      //  console.log(val);
      this.$emit("tagClick",val)
     },
     clearHistory(){
        ClearHistory().then(res=>{
          if(res.data.errno==0){
            //  console.log(res);
            //  alert("删除成功");
            //提示语
            this.$toast.success('成功文案');
            //隐藏历史记录的这个盒子
            setTimeout(()=>{
              this.isShowHistory=false;

            },1000)
           
          }
        })
     }
   }
};
</script>

<style lang="less" scoped>
.his-hot {
  background-color: #fff;
  padding: 0.1rem;
  margin-bottom: .2rem;
  .hd {
    display: flex;
    justify-content: space-between;
    font-size: 0.24rem;
    margin-bottom: 0.8rem;
    h4 {
      font-size: 0.2rem;
    }
    margin-bottom: 0.05rem;
  }
  .van-tag{
      margin-right: .09rem;
      margin-bottom: .05rem;

      padding:0.03rem 0.05rem;
  }
  .red{
      color:red;
  }
}
</style>
