<template>
  <div class="brand">
    <Navbar/>
    <div class="banner">
      <img v-lazy="brand.list_pic_url" alt="" />
      <h2>{{ brand.name }}</h2>
    </div>
    <p>{{ brand.simple_desc }}</p>
  </div>
  
</template>

<script>
import {brandListData} from "@/request/api";
import Navbar from '@/components/Navbar.vue'
export default {
data(){
     return{
        brand:{}, 
     }
 },
 components: {
    Navbar
  },
 created(){
     brandListData({
         id:this.$route.query.id,
     }).then((res)=>{
         if(res.data.errno==0){
        //    console.log(res);
           this.brand=res.data.data.brand;
         }
     })
 },
 
}
</script>

<style lang="less" scoped>
.brand{
  
.banner {
  width: 100%;
  position: relative;
  img {
    display: block;
    width: 100%;
  }
  h2 {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    height: 30px;
    line-height: 30px;
    margin: auto;
    font-size: 20px;
    text-decoration: underline;
    color: #fff;
  }
}
p {
  background: #fff;
  font-size: 18px;
  text-align: center;
  padding: 20px 10px;
  line-height: 26px;
  color: #666;
}
}
</style>