<template>
  <div class="channel">
    
      <van-sticky>
        <div class="nav">
        <van-icon class="arrow" name="arrow-left" @click="back" />
        <span class="title">商城</span>
        </div>
      </van-sticky>
      <van-tabs v-model="activeName" @click="onChange">
      <van-tab :title="item.name" v-for="(item,index) in brotherCategory" :key="index" :name="item.name"></van-tab>
      </van-tabs>
      <div class="desc">
           <p>{{name}}</p>
          <span>{{frontdesc}}</span>

      </div>
      <!-- <van-tabs v-model="active">
      <van-tab title="标签 1">内容 1</van-tab>
      <van-tab title="标签 2">内容 2</van-tab>
      <van-tab title="标签 3">内容 3</van-tab>
      <van-tab title="标签 4">内容 4</van-tab>
      </van-tabs> -->


<ul>
         <li v-for="item in goods" :key="item.id" @click="toProductDetail(item.id)">
             <img v-lazy="item.list_pic_url" alt="" style="display:block;" width="100%"/>
             <div class="title">{{item.name}}</div>
             <div class="price">{{item.retail_price | RMBformat}}</div>
         </li>
</ul>
    </div>
  
</template>

<script>
import { GetCategoryData,CategoryProductData } from "@/request/api";
export default {
  name: "Channel",
  data() {
    return {
      //选中状态
       activeName:'',
       //标题栏种类数组
       brotherCategory:[],
       //当前种类标题描述
       currentCategory:{},
       id:'',
       //当前种类标题描述
       frontdesc:"",
       //当前种类名称
      name:"",
      //当前种类id
      id:"",
      categoryId:"",
      page:1,
      size:1000,
      //产品名称
      productName:"",
      //产品图片
      productUrl:"",
      //产品价格
      productPrice:"",
      goods:{},


    };
  },
  methods: {
    onChange(id){
      
      // console.log(this.brotherCategory);

    //id打印出来是栏名称
      // console.log(id);
      
    this.brotherCategory.map((item)=>{
      if(item.name==id){
        this.frontdesc=item.front_desc
        this.name=item.name;
        this.id=item.id
      }
    })
        
    
       this.changeCategory(this.id);
       
    },
    back() {
      this.$router.go(-1);
    },
    //   onClick(name, title) {
          
    //        console.log(name);
    //        console.log(title);
    //         // this.name=title;
    //         // this.frontdesc=this.currentCategory.front_desc;
            
        
    //   // console.log(this.currentCategory);

    //   //注意forEach用前需要封装
    //   // this.currentCategory.forEach((item) => {
    //   //   this.id=item.id;

    //   this.changeCategory(this.name);
    // },
    //定义一个方法，onclik事件触发后渲染产品列表
    changeCategory(id){
         CategoryProductData({
        categoryId: id,
        page: this.page,
        size: this.size,
       }).then(res=>{
         if(res.data.errno==0){
            // console.log(res);
            this.goods=res.data.data.goodsList;
            this.productPrice=res.data.data.retail_price;
            this.productUrl=res.data.data.list_pic_url;
        
         
         }
       })
    },
    //跳转到详情页
    toProductDetail(id){
        this.$router.push("/ProductDetail?id="+id);
        setTimeout(()=>{
          //强制刷新页面
          this.$router.go(0);
        },100)
      }
  },
  created(){
    GetCategoryData({
      id: this.$route.query.id,
    }).then((res) => {
      //  console.log(res.data.data);
      this.brotherCategory = res.data.data.brotherCategory;
      this.currentCategory = res.data.data.currentCategory;
      //  console.log(this.currentCategory);
       this.frontdesc=res.data.data.currentCategory.front_desc;
       this.name=res.data.data.currentCategory.name;

       //刚加载页面是tab栏选中状态
         this.brotherCategory.forEach((item, index) => {
        if (this.$route.query.id == item.id) this.activeName = item.name;
      });  
      // console.log(this.activeName);
      this.changeCategory(this.$route.query.id);

      
    });
  }
};
</script>

<style lang="less" scoped>
.nav {
  position: relative;
  width: 100%;
  background-color: #fff;
  border-bottom: 1px solid #ccc;
  height: 0.45rem;
  line-height: 0.45rem;
  z-index: 999;
}
.title {
  text-align: center;
  font-size: 0.18rem;
  display: block;
}
.arrow {
  position: absolute;
  color: darkred;
  font-size: 0.2rem;
  left: 0.2rem;
  top: 0.15rem;
}
.desc{
  height: 0.65rem;
  background-color: #fff;
  span{
    display: block;
    text-align:center;
  }
  p{
    text-align: center;
    font-size: 0.17rem;
    margin-bottom: 0.1rem;
    padding-top: 0.1rem;
    font-weight: 600;
  }
}
  ul {
  background-color: #efefef;
  padding: .1rem 2%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  li {
    width: 49%;
    background-color: #fff;
    text-align: center;
    line-height: .3rem;
    margin-bottom: .1rem;
    .title {
      // 最多显示一行的文字，多余的内容会被省略
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .price {
      color: darkred;
    }
  }
}
</style>
