<template>
  <div>
    <van-dropdown-menu>
      <van-dropdown-item title="综合" disabled />
      <van-dropdown-item @change="priceChange" title="价格" v-model="priceVal" :options="option2" />
      <van-dropdown-item @change="categoryChange" title="筛选" v-model="categoryVal" :options="filterCategory" />
    </van-dropdown-menu>
    <van-empty v-if="goodsList.length==0" image="search" description="没有此类商品" />
    <Products v-else :goodsList="goodsList"/>
  </div>
</template>

<script>
import Products from "./Products"
export default {
  data() {
    return {
      priceVal:" ",//如果为空则默认是按id排序
      categoryVal:"",
     //categoryVal:1005000,//相当于value2,这个的值是哪一项的value，就表示这一项被勾上。
      option2: [
        { text: "价格由高到低", value: "desc" },
        { text: "价格由低到高", value: "asc" },
      ],
    };
  },
  props:["filterCategory","goodsList"],//filterCategory相当于options2[{text:"全部",value：0}，{text:"家居",value:100500}]
  components:{
    Products,
  },
  methods:{
    categoryChange(value){
      // console.log(value);//value就是分类id
      this.$emit("categoryChange",value)
    },
    priceChange(value){
      //  console.log(value);//desc 或asc
       this.$emit("priceChange",value)
    }
  },
  //遍历找到了默认勾选的，即默认勾选全部。 如果不想遍历直接把categoryVal:0,
  beforeUpdate(){
    //数据挂载完毕，遍历filterCateory数组，看看哪一项的checked为true
    //遍历的时候 遇到checked为true时，就把this.categoryVal改成它的value值，这样这一项就被勾选了
    
    this.filterCategory.map(item=>{
     if(item.checked){
      // console.log("item",item);
      this.categoryVal=item.value
      return
    }
    })
  }
};
</script>

<style></style>
