<template>
  <div>
    <van-row @click="openModal">
      <van-col span="6">
        <img :src="avatarSrc" alt="" />
      </van-col>
      <van-col span="15">{{ nickname }}</van-col>
      <van-col span="3">
      <van-icon :name="Logined ? 'cross' : 'arrow'" @click="loginOut" />
      </van-col>
    </van-row>
    <van-grid :column-num="3" square>
      <van-grid-item icon="photo-o" text="文字" />
      <van-grid-item icon="photo-o" text="文字" />
      <van-grid-item icon="photo-o" text="文字" />
      <van-grid-item icon="photo-o" text="文字" />
    </van-grid>
    <!-- 淡入效果 -->
    <transition name="van-fade">
      <div class="login-modal" v-show="isShowModal">
        <div class="close-modal" @click="isShowModal = !isShowModal"></div>
        <van-form @submit="onSubmit" class="login-form">
          <van-field
            v-model="username"
            name="用户名"
            label="用户名"
            placeholder="用户名"
            :rules="[{ required: true, message: '请填写用户名' }]"
          />
          <van-field
            v-model="password"
            type="password"
            name="密码"
            label="密码"
            placeholder="密码"
            :rules="[{ required: true, message: '请填写密码' }]"
          />
          <div style="margin: 16px;">
            <van-button round block type="info" native-type="submit"
              >提交</van-button
            >
          </div>
        </van-form>
      </div>
    </transition>
  </div>
</template>

<script>
import { GoLogin } from "@/request/api";
export default {
  name: "User",
  data() {
    return {
      avatarSrc: require("../assets/avatar.png"),
      username: "",
      password: "",
      isShowModal: false,
      nickname: "点击登录",
      // 判断用户是否登录
      Logined: false,
    };
  },
  created(){
    //组件刚创建时需要判断用户有没有登录，获取localStorage有没有token值
    //如果有token，就表示登录，就应该填上用户信息
    let token = localStorage.getItem("token");
    if(token){
      //表示已经有登录状态，就应该填上用户信息
      let userInfo = JSON.parse(localStorage.getItem("userInfo"));
      this.nickname = userInfo.nickname;
      this.avatarSrc = userInfo.avatar;
       this.Logined=true;
    }
  },
  methods: {
    onSubmit(values) {
      // console.log("submit", values);
      // console.log('username',values["用户名"]);
      // console.log('pwd',values['密码']);
      let username = values["用户名"];
      let pwd = values["密码"];
      //  GoLogin({username:values["用户名"],pwd:values["密码"]})
      GoLogin({ username, pwd }).then((res) => {
        if (res.data.errno == 0) {
          // console.log(res.data.data);

          //登录成功了
          // 登录的业务逻辑
          // 1、提示框提示登录成功
          this.$toast.success("登录成功");
          // 2、把token保存到本地存储
          localStorage.setItem("token", res.data.data.token);
          localStorage.setItem(
            "userInfo",
            JSON.stringify(res.data.data.userInfo)
          );
           this.Logined=true;
          // 3、1s后关闭模态窗口
          setTimeout(() => {
            this.isShowModal = !this.isShowModal;
            // 4、把拿到的userinfo用户信息写到页面上
            this.nickname = res.data.data.userInfo.nickname;
            // console.log(this.nickname);
            this.avatarSrc = res.data.data.userInfo.avatar;
          }, 1000);
        //  console.log(this.Logined);

           if (this.$store.state.ifgoBack==false) {
            // 显示即将回到上一页
            this.$toast.loading("即将回到上一页")
            // 要跳回上一页
            setTimeout(()=>{
              this.$router.back();
              // 顺手重新改为false
              this.$store.commit("changeIfgoBack", false);
            }, 2000)
            console.log(this.$store.state.ifgoBack);
           }
           
        // else {
        //   this.$toast.fail(res.errmsg)
        // }
        }
      });
    },
    //退出登录
    loginOut(){
      // console.log(222);
       if (this.Logined) {
        this.$dialog
          .confirm({
            title: "退出账号确认",
            message: "您是否想要退出当前账号？",
          }) .then(() => {
            // 清除token
            localStorage.removeItem('token')
            localStorage.removeItem('userInfo')
            // 刷新当前页面
            
            this.$router.go(0)
          })
          .catch(err => err)
      } else {
        // 会冒泡到父元素van-row上有打开模态框的点击事件，所以在这里不用在写了
        // 代表未登录，打开模态框
        // this.showModal = true
      }
    },
    openModal(){
      //判断用户是否登录，如果登录就直接return，没有登录就isShowModal取反
      let token=localStorage.getItem("token");
      if(token){
        return
      }
      this.isShowModal=!this.isShowModal
    }
  },
};
</script>

<style lang="less" scoped>
.van-row {
  padding: 0.2rem 4%;
  background-color: #333;
  color: #fff;
  .van-col {
    line-height: 0.7rem;
    font-size: 0.15rem;
    img {
      width: 0.7rem;
      border-radius: 50%;
      display: block;
    }
    &:last-child {
      text-align: right;
    }
  }
}
.login-modal {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  // background-color: rgba(0, 0, 0, 0.5);
  z-index: 9;
  .login-form {
    width: 90%;
    background-color: #fff;
    padding-bottom: 0.2rem;
    position: absolute;
    left: 50%;
    top: 35%;
    margin-left: -45%;
  }
  .close-modal {
    width: 100%;
    height: 100%;

    background-color: rgba(0, 0, 0, 0.5);
  }
}
</style>
