<template>
 <div class="topic">
   <!-- <van-loading size="30px" vertical>加载中...</van-loading> -->
    <div v-for="item in data" :key="item.id" class="box">
      <img class="pic" v-lazy="item.scene_pic_url" alt="" />
      <div class="title">{{ item.title }}</div>
      <div class="tip">{{ item.subtitle }}</div>
      <div class="price">{{ item.price_info}}起</div>
    </div>
    <!-- change事件是在页码变化时触发 -->
    <van-pagination
      v-model="currentPage"
      :items-per-page="items_per_page"
      :page-count="totalPages"
      mode="simple"
      @change="changeNum"
    />
   <BackTop/>
  </div>
</template>

<script>
import {GetTopicList} from "@/request/api"
import BackTop from '@/components/BackTop.vue'
export default {
data(){
  return{
      currentPage: 1,
      totalPages: "2",
      items_per_page: 10,
      data: [],
      count:'',
  }
},
components:{
 BackTop 
},
methods:{
  
   changeNum(){
     this.topicFun();
   },

   topicFun(){
      GetTopicList({
        page: this.currentPage,
        size: this.items_per_page,
      }).then((res) => {
        if (res.data.errno == 0) {
          // console.log(res);
         
          this.totalPages=res.data.data.totalPages;
          this.items_per_page=res.data.data.pageSize;
          this.currentPage =res.data.data.currentPage;
          this.count=res.data.data.count;
          this.data=res.data.data.data;
          // 返回顶部
          // document.documentElement.scrollTop = 0;
        }
      })
   }
},
created(){
  this.topicFun()
 }
}
</script>

<style lang="less" scoped>
.van-pagination__item {
  color: darkred;
}
.van-pagination__item--disabled {
  color: #333;
}
.van-pagination__page-desc {
  display: none;
}
.topic {
  padding-bottom: 100px;
  .van-loading{
  position: fixed;
  top:50%;
  left:50%;
  margin-left: -12px;
  margin-top: -12px;
  z-index: 99;
 background-color:rgba(0,0,0,0.6);

}
  .box {
    background-color: #fff;
    text-align: center;
    padding-bottom: 10px;
    margin-bottom: 20px;

    .pic {
      width: 100%;
    }
    .title {
      font-size: 18px;
      margin: 10px 0;
    }
    .tip {
      font-size: 16px;
      margin: 20px 0;
      color: rgb(31, 31, 31);
      overflow: hidden;
      text-overflow: ellipsis;
      /* 弹性伸缩盒子模型显示 */
      display: -webkit-box;
      /* 限制在一个块元素显示的文本的行数 */
      -webkit-line-clamp: 1;
      /* 设置或检索伸缩盒对象的子元素的排列方式 */
      -webkit-box-orient: vertical;
    }
    .price {
      color: rgb(155, 0, 0);
      margin-bottom: 10px;
    }
  }
}
/deep/.van-pagination__page-desc {
  display: none;
}
/deep/.van-pagination__item  {
  color: darkred;
}
