<template>
  <div>
    <Navbar/>
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="item in gallery" :key="item.id">
        <img v-lazy="item.img_url" alt="" width="100%" style="display:block" />
      </van-swipe-item>
    </van-swipe>
    <Tips />
    <div class="info">
      <div class="title">{{ info.name }}</div>
      <div class="goods_brief">{{ info.goods_brief }}</div>
      <div class="retail_price">{{ info.retail_price | RMBformat }}</div>
    </div>
    <van-cell title="选择规格" is-link @click="isSkuShow=true" />
    
    <div class="attribute">
      <h3>商品参数</h3>
      <ul>
        <li v-for="(item, key) in attribute" :key="key">
          <div class="l">{{ item.name }}</div>
          <div class="r van-ellipsis">{{ item.value }}</div>
        </li>
      </ul>
    </div>
    <div class="goods_desc" v-html="goods_desc"></div>
    <Products :goodsList="goodsList" />
    <!-- 购物车sku组件 -->
     <!-- 通过ref获取sku结构 -->
    <van-sku
      ref="sku"  
      v-model="isSkuShow"
      :sku="sku"
      :goods="goods"
      :hide-stock="sku.hide_stock"
    />
    <AppGoodActive @goToCart="goToCart" :goodsCount="goodsCount" />
  </div>
</template>

<script>
import { GetGoodsDetailData, GetGoodsRelatedData, GetGoodsCount,AddProductToCart } from "@/request/api";
import Tips from "@/components/Tips";
import Products from "@/components/Products";
import AppGoodActive from "@/components/AppGoodActive";
import Navbar from '@/components/Navbar.vue'
export default {
  data() {
    return {
      isSkuShow:false,
      sku:{
          tree:[],
          hide_stock:false,  // 是否隐藏剩余库存
           price: '0', // 默认价格（单位元）
           stock_num: 0, // 商品总库存
      },
      goods:{
           picture:""
      },





      //轮播图数据
      gallery: [],
      //商品信息描述
      info: {},
      //商品参数
      attribute: [],
      //商品信息（图片）
      goods_desc: "",
      //常见问题
      issue: [],
      //相关产品列表
      goodsList: [],
      //购物车产品数量
      goodsCount:0,
      productList:[]
    }
  },
  components: {
    Tips,
    AppGoodActive,
    Products,
    Navbar
  },
  created() {
    //发送请求，请求这个产品的详情数据
    GetGoodsDetailData({
      id: this.$route.query.id,
    }).then((res) => {
      // console.log(res);
      //  console.log(this.gallery);
      // let {gallery}=res.data.data;
      // this.gallery=gallery;
      this.gallery = res.data.data.gallery;
      this.info = res.data.data.info;
      this.attribute = res.data.data.attribute;
      this.goods_desc = this.info.goods_desc;
      //   console.log(this.goods_desc);
      this.issue = res.data.data.info;
      this.productList=res.data.data.productList
      // console.log(this.productList);




      //sku数据
      this.goods.picture = this.info.list_pic_url
      this.sku.price = this.info.retail_price
      this.sku.stock_num = this.info.goods_number
    });
    //发送请求获取相关产品数据（大家都在看模块）
    GetGoodsRelatedData({
      id: this.$route.query.id,
    }).then((res) => {
      //  console.log(res.data.data);
      this.goodsList = res.data.data.goodsList;
      
    });
  // 先判断有没有token
    let token = localStorage.getItem('token');
    if (token) {
      // 调用获取购物车数量的函数
      this.GetCount();
       //获取购物车产品数量
  
    }

  //   //获取购物车产品数量
  //  GetGoodsCount().then(res=>{
  //    if(res.data.errno==0){
  //     //  console.log(res);//注意：goodsCount数值登录与后有关
  //     this.goodsCount=res.data.data.cartTotal.goodsCount
  //     // console.log(this.goodsCount);
  //    }
  //  })
  },
  methods:{
      //获取购物车产品数量

   GetCount(){
    GetGoodsCount().then(res=>{
     if(res.data.errno==0){
      //  console.log(res);//注意：goodsCount数值登录与后有关
      this.goodsCount=res.data.data.cartTotal.goodsCount
      // console.log(this.goodsCount);
     }
   })
    },
    goToCart(){
      //如果sku没有展示的情况下，点击购物车按钮，展示sku
      //如果sku已经在展示的状态，点击购物车按钮，跳转到购物车页面
      if(this.isSkuShow){
          //加入购物车
          // console.log("执行加入购物车的操作");
          //获取要添加的商品的数量
          // console.log(this.$refs.sku.getSkuData().selectedNum);
          //发起加入购物车请求
           let token = localStorage.getItem('token')
           if(token){
          AddProductToCart({
            goodsId:this.$route.query.id,
            productId:this.productList[0].id,
            number:this.$refs.sku.getSkuData().selectedNum
          }).then(res=>{
            if(res.data.errno==0){
              // console.log(res);
              //添加到购物车后做两件事情
              //1、更新购物车数量
              this.goodsCount=res.data.data.cartTotal.goodsCount
              //2、跳转到购物车页面
              this.$toast.success("加入购物车成功");
              setTimeout(()=>{
                // this.$router.push({name:'Cart'});
                 this.$router.push("/cart");
              },2000)

            }
          })
           }else{
              // this.$router.push("/user");
               this.$toast.loading("请先登录")
               setTimeout(()=>{
                // this.$router.push({name:'Cart'});
                 
                 this.$router.push("/user");
              },1500)

           }

      }else{
        this.isSkuShow=true
      }
    },
  },
  watch: {
    $route(to, from) {
      if (to.path == from.path) return;
      this.$router.go(0); // 刷新页面
    },
  }

};
</script>

<style lang="less" scoped>
.info {
  background-color: #fff;
  text-align: center;
  line-height: 0.4rem;
  padding: 0.1rem 0;
  border-bottom: 1px solid #ccc;
  .title {
    font-size: 0.2rem;
  }
  .retail_price {
    color: darkred;
  }
}
.attribute {
  background-color: #fff;
  margin-top: 0.2rem;
  padding: 0.1rem 0.168rem;
  h3 {
    font-size: 0.16rem;
    line-height: 0.4rem;
  }
  li {
    height: 0.4rem;
    line-height: 0.4rem;
    background-color: #efefef;
    display: flex;
    margin-bottom: 0.1rem;
    border-radius: 0.05rem;

    .l {
      width: 30%;
      text-align: right;
      margin-right: 0.1rem;
    }
    .r {
      flex: 1;
    }
  }
}
//深度样式写法，穿透ui组件的样式，直接添加在标签上
/deep/.goods_desc {
  img {
    display: block;
    width: 100%;
  }
}
</style>
