<template>
  <div class="categoryBar">
    <van-search
      v-model="SearchVal"
      placeholder="请输入搜索关键词"
      disabled
      shape="round"
      @click="handleClickSearch"
    />
    <div class="main">
    <transition name="slide">
      <router-view></router-view>
     </transition>
    <van-sidebar v-model="activeKey">
      <van-sidebar-item :title="item.name"  v-for="item in categoryList" :key="item.id" @click="ChangeCategory(item.id)"/>
    </van-sidebar>
    <div class="right">
        <div class="r-top">
         <img v-lazy="banner_url" alt="" style="display:block;" width="100%"/>
             <div class="desc">{{front_name}}</div>
            
        </div>
        <div class="r-bottom">
            <span></span>
          <h3>{{ name }}</h3>
        </div>
         <van-grid :column-num="3">
              <van-grid-item v-for="item in subCategoryList" :key="item.id" :icon="item.wap_banner_url" :text="item.name" />
           </van-grid>
    </div>
  </div>
  </div>
</template>

<script>
import {GetAllCategory,GetCurrentCategory} from "@/request/api"
export default {
  name: "Category",
  data() {
    return {
      SearchVal: "夹克衫",
        activeKey: 0,
        //分类页左侧数组
        categoryList:[],
        //分类第一项
        currentCategory:{},
        //标题名
        name:'',
        //banner标题
        front_name:'',
        //banner路径
        banner_url:'',
        //子类目数组
        subCategoryList:[],
    };
  },
  methods: {
    handleClickSearch() {
      setTimeout(() => {
        this.$router.push("/category/SearchPopup");
      }, 500);
    },
    //侧边栏切换时触发
    ChangeCategory(val) {
    //  console.log(val);
        GetCurrentCategory({
            id:val,
        }).then(res=>{
            if(res.data.errno==0){
                console.log(res);
                 this.currentCategory=res.data.data.currentCategory;
                //console.log(this.currentCategory);
                this.name=res.data.data.currentCategory.name;
                this.front_name=res.data.data.currentCategory.front_name;
                this.banner_url=res.data.data.currentCategory.banner_url;
                this.subCategoryList=res.data.data.currentCategory.subCategoryList 
            }
        })
    }
  },
  created(){
      GetAllCategory().then(res=>{
          if(res.data.errno==0){
            //   console.log(res);
              this.categoryList=res.data.data.categoryList;
              this.currentCategory=res.data.data.currentCategory;
            //   console.log(this.currentCategory);
              this.name=res.data.data.currentCategory.name;
              this.front_name=res.data.data.currentCategory.front_name;
              this.banner_url=res.data.data.currentCategory.banner_url;
              this.subCategoryList=res.data.data.currentCategory.subCategoryList
          }
      })
  }
   
};
</script>

<style lang="less" scoped>
.slide-enter,
.slide-leave-to {
  right: -100%;
}
.slide-enter-active,
.slide-leave-active {
  transition: right 0.5s linear;
}
.slide-enter-to,
.slide-leave {
  right: 0;
}
.categoryBar{
    background-color: #ffffff;
    height: 100vh;
    width: 100%;
    padding-bottom: 0.7rem;
.main{
    display: flex;
}
.van-sidebar{
    width: 20%;
    // background-color: skyblue;
}
.right{
    width: 80%;
    // background-color:#ffffff;
   .r-top{ 
       position: relative;
       img{
         width: 96%;
         margin-left: 2%;
         height: 1.5rem;
         border-radius: 0.05rem;
          box-shadow: 0 0 0.06rem #000;
           filter: brightness(0.6);
       }
       .desc{
           position: absolute;
           top: 50%;
           left: 50%;
           transform: translate(-50%,-50%);
           color:#ffffff;
           font-size: 0.18rem;
           
       }
   }
   .r-bottom{
        
      width: 100%;
      height: 0.5rem;
      position: relative;
      background: #fff;
      font-size: 0.16rem;
      span {
        width: 50%;
        height: 0.02rem;
        background-color: #ccc;
        position: absolute;
        margin: auto;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
      }
      h3 {
        font-weight: normal;
        width: 30%;
        height: 40px;
        background-color: #fff;
        position: absolute;
        margin: auto;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        line-height: 40px;
        text-align: center;
        color: #000;
      }
    
   }
   
}
}
</style>
